* {
  --font-family-sans-serif: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans","Liberation Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
  --font-family-monospace: SFMono-Regular,Menlo,Monaco,Consolas,"Liberation Mono","Courier New",monospace;
  color: #070707;
}

html {
  background: #D19FA8;
}
.main-container {
  text-align: center;
  background: #D19FA8;
}

.progress {
  margin: 1em;
  height: 3em;
  padding: 0;
}

.make-green {
  background: #8DC76B;
  color: #070707;
}

.make-light-green {
  background: #A2D289;
  color: #070707;
}

.make-red {
  background: #8B5559;
}

.make-maroon {
  background: #380E05;
  color: #C2B5AE;
}

.counter {
  background: #380E05;
  color: #C2B5AE;
  border-radius: 100%;
  width: 3em;
  height: 3em;
  line-height: 3em;
  box-shadow: 0 0 1em .5em rgba(0,0,0,0.2);
}

.button-green {
  background: #A2D289;
  padding: 1.5em;
  transition:  500ms;
  box-shadow: 0 0 1em .5em rgba(0, 0, 0, 0.1);
}

.button-green:hover {
  background: #8DC76B;
  box-shadow: .5em .5em 1em .5em rgba(0, 0, 0, 0.2);
}

.card {
  padding: 1.7em;
  margin: 2em;
  border-radius: 10px;
  box-shadow: 0 0 1em .5em rgba(0,0,0,0.2);
}

a {
  color: #A2D289;
}

a:hover {
  color: #8DC76B;
  text-decoration: none;
}